import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {OrderResponse} from 'src/app/core/models/seller/order/response/OrderResponse';

@Component({
	selector: 'app-order-show-table',
	templateUrl: './order-show-table.component.html',
	styleUrls: ['./order-show-table.component.less']
})
export class OrderShowTableComponent implements OnInit {
	@Input() pageResult: PagedResult<OrderResponse> = {} as PagedResult<OrderResponse>;
	@Input() listOfData: Array<OrderResponse> = [];
	@Input() isLock: boolean = false;
	@Output() pageIndex = new EventEmitter<number>();
	@Output() orderId: number;

	pageCurrentIndex = 1;
	//host: string = environment.apiUrl;
	constructor() {
    this.pageIndex.emit(1);
	
  }

	ngOnInit(): void {}
  onScroll() {
		this.pageCurrentIndex = this.pageCurrentIndex + 1;
		this.pageIndex.emit(this.pageCurrentIndex);
	}
}
