import {SellerOrderService} from './../../../../../../../../core/services/seller/seller-order/seller-order.service';
import {Component, OnInit} from '@angular/core';

import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {takeUntil} from 'rxjs/operators';
import {OrderResponse} from 'src/app/core/models/seller/order/response/OrderResponse';

@Component({
	selector: 'app-order-tab-all',
	templateUrl: './order-tab-all.component.html',
	styleUrls: ['./order-tab-all.component.less'],
	providers: [DestroyService]
})
export class OrderTabAllComponent implements OnInit {
	loading = false;
	startOfIndex = 1;
	tabIndex = 0;
	pageResult: PagedResult<OrderResponse> = {} as PagedResult<OrderResponse>;
	listOfData: Array<OrderResponse> = [];
	constructor(
		private destroy$: DestroyService,
		private sellerOrderService: SellerOrderService,
		private notificationService: NotificationService
	) {}

	getOrders(pageIndex: number, typeOrder: number) {
		this.loading = true;
		this.sellerOrderService
			.getAllProductPaging(pageIndex, typeOrder)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					console.log(result);
					if (result.resultObj) {
						this.pageResult = result.resultObj;
						console.log(this.listOfData);
						Array.prototype.push.apply(this.listOfData, result.resultObj.items);
						this.loading = false;
						console.log(this.listOfData);
					}
					this.loading = false;
				},
				(error) => {
					this.loading = false;
					this.notificationService.showErrorNotification(error.message);
				}
			);
	}
	onScroll() {
		this.startOfIndex = this.startOfIndex + 1;
		console.log(this.startOfIndex);
		this.getOrders(this.startOfIndex, this.tabIndex);
	}
	changeTab(tabIndex: number) {
		this.tabIndex = tabIndex;
		this.startOfIndex = 1;
		this.loading = true;
		this.pageResult = {} as PagedResult<OrderResponse>;
		this.listOfData = [];
		this.getOrders(this.startOfIndex, this.tabIndex);
	}
	ngOnInit(): void {
		this.getOrders(this.startOfIndex, this.tabIndex);
	}
	
}
